/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-size: 1.6rem;
  line-height: 1.8;
  background: #002222;
  margin: 0;
}

section {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
section .container {
  margin-top: 2rem;
  min-height: 100%;
}
@media (min-width: 1007px) {
  section .container {
    margin-top: 0;
    height: 85vh;
  }
}

.container {
  position: relative;
  width: 95%;
  max-width: 240rem;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (min-width: 1007px) {
  .container {
    margin-top: 0;
    flex-direction: row;
    align-items: center;
  }
}

a {
  font-family: "Bebas Neue", cursive;
  font-weight: 700;
  text-decoration: none;
  text-transform: uppercase;
}

p {
  font-family: "Lato", sans-serif;
  font-size: 1.5rem;
}

h1,
h2,
h3,
h4 {
  font-family: "Lato", sans-serif;
  margin: 10em;
  color: black;
  margin: 0;
  font-weight: 500;
  text-transform: uppercase;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 2.8rem;
}

h3 {
  font-size: 2.4rem;
}

h4 {
  font-size: 1.8rem;
  margin: 0;
}

#about,
#work {
  background-color: #FFF0D3;
}

.bg-img {
  max-width: 100%;
  height: 100vh;
}

.item-unvisible {
  visibility: hidden;
}
@media (min-width: 1007px) {
  .item-unvisible {
    visibility: visible;
  }
}

.item-visible {
  visibility: visible;
}

.text {
  text-align: justify;
  line-height: 1.8;
  padding: 2rem;
}
.text h4 {
  color: #000000;
  font-size: 0.95em;
}
@media (min-width: 1007px) {
  .text {
    width: 90%;
    height: auto;
    margin: 0.5rem 1.5rem;
    margin-right: 3rem;
    padding: 0 5rem;
    text-align: justify;
  }
  .text h4 {
    font-size: 1.3em;
  }
}